import React from 'react';

const Footer=()=>{
	const currentYear = new Date().getFullYear();
    return(
        <footer className="footer_area p_60">
        	<div className="container">
        		<div className="row footer_inner text-center justify-content-center">
        			<div className="col-lg-6 col-sm-12">
        				<aside className="f_widget ab_widget">
        					<div className="f_title">
        						<h3>About Me</h3>
        					</div>
        					<p>Do you want to be even more successful? Learn to love learning and growth. The more effort you put into improving your skills,</p>
        				</aside>
						<aside className="f_widget social_widget">
        					<p>Let us be social</p>
        					<ul className="list">
        						<li><a href="#"><i className="fa fa-facebook"></i></a></li>
        						<li><a href="#"><i className="fa fa-twitter"></i></a></li>
        						<li><a href="#"><i className="fa fa-dribbble"></i></a></li>
        						<li><a href="#"><i className="fa fa-behance"></i></a></li>
        					</ul>
        				</aside>
						<aside className="f_widget ab_widget mt-4">
        					<p className='mb-0'>Copyright &copy; {currentYear} All rights reserved by <a href="#" target="_blank">Ankush</a></p>
        				</aside>
        			</div>
        		</div>
        	</div>
        </footer>
    );
}

export default Footer;