import React from "react";

const Skill = () => {
  return (
    <div className="skill_main">
      <div className="skill_item">
        <div className="d-flex justify-content-between">
          <h4>After Effects</h4>
          <h4>
            <span className="counter">85%</span>
          </h4>
        </div>
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            style={{ width: "85%" }}
            aria-valuenow={85}
            aria-valuemin={0}
            aria-valuemax={100}
          ></div>
        </div>
      </div>
      <div className="skill_item">
        <div className="d-flex justify-content-between">
          <h4>Photoshop</h4>
          <h4>
            <span className="counter">90%</span>
          </h4>
        </div>
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            style={{ width: "90%" }}
            aria-valuenow={90}
            aria-valuemin={0}
            aria-valuemax={100}
          ></div>
        </div>
      </div>
      <div className="skill_item">
        <div className="d-flex justify-content-between">
          <h4>Illustrator</h4>
          <h4>
            <span className="counter">70%</span>
          </h4>
        </div>
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            style={{ width: "70%" }}
            aria-valuenow={70}
            aria-valuemin={0}
            aria-valuemax={100}
          ></div>
        </div>
      </div>
      <div className="skill_item">
        <div className="d-flex justify-content-between">
          <h4>Sublime</h4>
          <h4>
            <span className="counter">95%</span>
          </h4>
        </div>
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            style={{ width: "95%" }}
            aria-valuenow={95}
            aria-valuemin={0}
            aria-valuemax={100}
          ></div>
        </div>
      </div>
      <div className="skill_item">
        <div className="d-flex justify-content-between">
          <h4>Sketch</h4>
          <h4>
            <span className="counter">75%</span>
          </h4>
        </div>
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            style={{ width: "75%" }}
            aria-valuenow={75}
            aria-valuemin={0}
            aria-valuemax={100}
          ></div>
        </div>
      </div>
    </div>
  );
};

export default Skill;
