import React, { useState, useEffect } from "react";
import imagesLoaded from "imagesloaded";
import Isotope from "isotope-layout";
import "isotope-packery";
import img1 from "../../../Assets/img/projects/projects-1.jpg";
import img2 from "../../../Assets/img/projects/projects-2.jpg";
import img3 from "../../../Assets/img/projects/projects-3.jpg";
import img4 from "../../../Assets/img/projects/projects-4.jpg";
import img5 from "../../../Assets/img/projects/projects-5.jpg";
import img6 from "../../../Assets/img/projects/projects-6.jpg";
import { useNavigate } from 'react-router-dom';

const Projectsfill = () => {
  
  const navigate = useNavigate();

  function handleButtonClick() {
    navigate('/Portfolio-details');
  }

  const [isotope, setIsotope] = useState(null);
  const [activeFilter, setActiveFilter] = useState("");

  useEffect(() => {
    const projectsInner = document.querySelector(".projects_inner");

    imagesLoaded(projectsInner, () => {
      const isotopeInstance = new Isotope(projectsInner, {
        layoutMode: "fitRows",
        animationOptions: {
          duration: 750,
          easing: "linear",
        },
      });

      setIsotope(isotopeInstance);
    });
  }, []);

  const handleFilter = (e) => {
    const { filter } = e.currentTarget.dataset;
    setActiveFilter(filter);

    isotope.arrange({
      filter,
      animationOptions: {
        duration: 450,
        easing: "linear",
        queue: false,
      },
    });
  };

  
  
  return (
    <>
    <div className="projects_fillter">
    <ul className="filter list">
          <li className={activeFilter === "" ? "active" : ""} data-filter="" onClick={handleFilter}>
            <a>All Categories</a>
          </li>
          <li className={activeFilter === ".brand" ? "active" : ""} data-filter=".brand" onClick={handleFilter}>
            <a>Branding</a>
          </li>
          <li className={activeFilter === ".work" ? "active" : ""} data-filter=".work" onClick={handleFilter}>
            <a>Creative Work </a>
          </li>
          <li className={activeFilter === ".web" ? "active" : ""} data-filter=".web" onClick={handleFilter}>
            <a>Web Design</a>
          </li>
        </ul>
      </div>
      <div className="projects_inner row">
        <div className="col-lg-4 col-sm-6 brand web">
          <div className="projects_item" onClick={handleButtonClick}>
            <img className="img-fluid" src={img1} alt="" />
            <div className="projects_text">
              <a href="portfolio-details.html">
                <h4>3D Helmet Design</h4>
              </a>
              <p>Client Project</p>
            </div>
          </div>
        </div>
        <div className="col-lg-4 col-sm-6 brand work">
          <div className="projects_item" onClick={handleButtonClick}>
            <img className="img-fluid" src={img2} alt="" />
            <div className="projects_text">
              <a href="portfolio-details.html">
                <h4>3D Helmet Design</h4>
              </a>
              <p>Client Project</p>
            </div>
          </div>
        </div>
        <div className="col-lg-4 col-sm-6 work">
          <div className="projects_item" onClick={handleButtonClick}>
            <img className="img-fluid" src={img3} alt="" />
            <div className="projects_text">
              <a href="portfolio-details.html">
                <h4>3D Helmet Design</h4>
              </a>
              <p>Client Project</p>
            </div>
          </div>
        </div>
        <div className="col-lg-4 col-sm-6 brand web">
          <div className="projects_item" onClick={handleButtonClick}>
            <img className="img-fluid" src={img4} alt="" />
            <div className="projects_text">
              <a href="portfolio-details.html">
                <h4>3D Helmet Design</h4>
              </a>
              <p>Client Project</p>
            </div>
          </div>
        </div>
        <div className="col-lg-4 col-sm-6 brand web">
          <div className="projects_item" onClick={handleButtonClick}>
            <img className="img-fluid" src={img5} alt="" />
            <div className="projects_text">
              <a href="portfolio-details.html">
                <h4>3D Helmet Design</h4>
              </a>
              <p>Client Project</p>
            </div>
          </div>
        </div>
        <div className="col-lg-4 col-sm-6 brand web">
          <div className="projects_item" onClick={handleButtonClick}>
            <img className="img-fluid" src={img6} alt="" />
            <div className="projects_text">
              <a href="portfolio-details.html">
                <h4>3D Helmet Design</h4>
              </a>
              <p>Client Project</p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Projectsfill;
