import React from "react";
import img1 from '../../../Assets/img/blog/cat-post/cat-post-1.jpg';
import img2 from '../../../Assets/img/blog/cat-post/cat-post-2.jpg';
import img3 from '../../../Assets/img/blog/cat-post/cat-post-3.jpg';
import { useNavigate } from 'react-router-dom';

const Catgories = () => {
  const navigate = useNavigate();

  function handleButtonClick() {
    navigate('/Blog-details');
  }
  return (
    <section className="blog_categorie_area">
      <div className="container">
        <div className="row">
          <div className="col-lg-4">
            <div className="categories_post">
              <img src={img1} alt="post" />
              <div className="categories_details">
                <div className="categories_text">
                  <a onClick={handleButtonClick}>
                    <h5>Social Life</h5>
                  </a>
                  <div className="border_line"></div>
                  <p>Enjoy your social life together</p>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-4">
            <div className="categories_post">
              <img src={img2} alt="post" />
              <div className="categories_details">
                <div className="categories_text">
                  <a onClick={handleButtonClick}>
                    <h5>Politics</h5>
                  </a>
                  <div className="border_line"></div>
                  <p>Be a part of politics</p>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-4">
            <div className="categories_post">
              <img src={img3} alt="post" />
              <div className="categories_details">
                <div className="categories_text">
                  <a onClick={handleButtonClick}>
                    <h5>Food</h5>
                  </a>
                  <div className="border_line"></div>
                  <p>Let the food be finished</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Catgories;
