import React, { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";

const Header = () => {
  const [navOffsetTop, setNavOffsetTop] = useState(0);
  const location = useLocation();

  const [isNavOpen, setIsNavOpen] = useState(false);

  const toggleNav = () => {
    setIsNavOpen(!isNavOpen);
  };

  useEffect(() => {
    setNavOffsetTop(document.querySelector(".header_area").offsetTop);
    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const handleScroll = () => {
    const scroll = window.pageYOffset;
    const headerArea = document.querySelector(".header_area");

    if (headerArea && scroll >= navOffsetTop) {
      headerArea.classList.add("navbar_fixed");
    } else if (headerArea) {
      headerArea.classList.remove("navbar_fixed");
    }
  };

  return (
    <header className="header_area">
      <div className="main_menu" id="mainNav">
        <nav className="navbar navbar-expand-lg navbar-light">
          <div className="container box_1620 my-0">
            <Link to="/" className="navbar-brand logo_h">
              Ankush
            </Link>
            <button
          className="navbar-toggler"
          type="button"
          onClick={toggleNav}
          aria-controls="navbarSupportedContent"
          aria-expanded={isNavOpen}
          aria-label="Toggle navigation"
        >
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
            </button>
            <div
              className={`collapse navbar-collapse ${isNavOpen ? "show" : ""}`}
              id="navbarSupportedContent"
            >
              <ul className="nav navbar-nav menu_nav ml-auto">
                <li className={`nav-item ${location.pathname === "/" ? "active" : ""}`}>
                  <Link to="/" className="nav-link">
                    Home
                  </Link>
                </li>
                <li className={`nav-item ${location.pathname === "/About" ? "active" : ""}`}>
                  <Link to="/About" className="nav-link">
                    About
                  </Link>
                </li>
                <li className={`nav-item ${location.pathname === "/Services" ? "active" : ""}`}>
                  <Link to="/Services" className="nav-link">
                    Services
                  </Link>
                </li>
                <li className={`nav-item ${location.pathname === "/Portfolio" ? "active" : ""}`}>
                  <Link to="/Portfolio" className="nav-link">
                    Portfolio
                  </Link>
                </li>
                <li className={`nav-item ${location.pathname === "/Blog" ? "active" : ""}`}>
                  <Link to="/Blog" className="nav-link">
                    Blog
                  </Link>
                </li>
                <li className={`nav-item ${location.pathname === "/Contact" ? "active" : ""}`}>
                  <Link to="/Contact" className="nav-link">
                    Contact
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </nav>
      </div>
    </header>
  );
};

export default Header;