import React from 'react';
import Address from './Contact-address/Contact-address';
import Form from './Contact-form/Contact-form';
import Maps from './Map/Map';

const Content=()=>{
    return(
        <section class="contact_area p_120">
            <div class="container">
                <Maps />
                <div class="row">
                    <Address />
                    <Form />
                </div>
            </div>
        </section>
    );
}

export default Content;